"use clien"
import menu_data from "@/utils/menu_data"
import {motion} from "framer-motion"
export default function Navbar(){
    return <motion.div>
        <div>
            <div>
                <img src="../../images/logo-dark.webp" alt="logo" />
            </div>
            <div>
                {menu_data.map(element=>{
                    return <div key={element.id}>
                        {element.title}
                    </div>
                })}
            </div>
        </div>
    </motion.div>
}